@import 'assets/scss/variables.scss';

.wrapper {
    & .time {
        & h1 {
            font-size: 10rem;
            color: #fff;
            background-color: #282828;
            padding: 2rem 1rem;
            margin: 0 1rem;
            border-radius: 2rem;
            min-width: 20rem;
            text-align: center;
        }
        & span {
            font-family: 'Courier New', Courier, monospace;
            display: block;
            width: 100%;
            text-align: center;
            font-size: 3.5rem;
            margin-top: 3rem;
        }
    }
}